.mde-ui.buttons {
  display: inline-flex;
  flex-direction: row;
  vertical-align: baseline;
  margin: 0 0.25em 0 0;
  box-shadow: none;
  font-size: 1rem;
}
.mde-ui.buttons .button {
  position: relative;
}
.mde-ui.buttons:after {
  content: '.';
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  font-size: 0;
}
.mde-ui.buttons .button {
  flex: 1 0 auto;
  border-radius: 0;
  margin: 0;
}
.mde-ui.buttons>.button,
.mde-ui.buttons>.mde-ui.button {
  box-shadow: inset 0 0 0 1px transparent, inset 0 0 0 0 rgba(34,36,38,0.15);
}
.mde-ui.buttons .button:first-child {
  border-left: none;
  margin-left: 0;
  border-top-left-radius: 0.28571429rem;
  border-bottom-left-radius: 0.28571429rem;
}
.mde-ui.buttons .button:last-child {
  border-top-right-radius: 0.28571429rem;
  border-bottom-right-radius: 0.28571429rem;
}
