.mde-ui.dropdown {
  display: inline-flex;
  flex-direction: column;
  vertical-align: baseline;
  margin: 0 0.25em 0 0;
  box-shadow: none;
  font-size: 1rem;
  outline: 0;
}
.mde-ui.dropdown>.button,
.mde-ui.dropdown>.mde-ui.button {
  box-shadow: inset 0 0 0 1px transparent, inset 0 0 0 0 rgba(34,36,38,0.15);
}
.mde-ui.dropdown .button {
  position: relative;
  flex: 1 0 auto;
  border-radius: 0.28571429rem;
  margin: 0;
  text-align: left;
  white-space: nowrap;
}
.mde-ui.buttons.dropdown.menubutton {
  flex-direction: column;
  position: absolute;
  top: 40px;
  z-index: 999;
  float: left;
  min-width: 180px;
  padding: 5px 0;
  text-align: left;
  outline: 0;
  background: #e0e1e2 none;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border: 1px solid rgba(0,0,0,0.15);
  border-radius: 0.28571429rem;
  box-shadow: 0 6px 12px rgba(0,0,0,0.175);
}
.mde-ui.buttons.dropdown.menubutton .button:first-child,
.mde-ui.buttons.dropdown.menubutton .button:last-child {
  border-radius: 0;
}
